export default {
	data() {
		return {
			phoneNum:''
		};
	},
	onLoad(e) {
		this.phoneNum = e.num;
	},
	methods:{
		toChangePwd(){
			uni.navigateTo({
				url:'/pages/changePwd/changePwd'
			})
		},
		toChangePhone(){
			uni.navigateTo({
				url:'/pages/changePhone/changePhone?phone='+this.phoneNum
			})
		}
	}
}
